import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import noteService from '../services/notes.js'
import './Notes.css'

const NoteItem = ({note, onChangeImportance}) => {

  return <div className='note'>
    <header>
      Note #{note.id}
    <button onClick={onChangeImportance}>change importance</button>
    </header>
    <p>{note.important?'ðŸ’¥':''}{note.content}</p>
    <footer>{note.date}</footer>
  </div>
}

const Notification = ({ msg }) => {
  return msg === null
    ? null
    : <div className='error'>{ msg }</div>
}

const Notes = (props) => {

  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('')
  const [isShowAll, setIsShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)


  useEffect(() => {
    noteService
      .getAll()
      .then(response => {
        setNotes(response.data)
      }) 
      // useEffectå¦‚æžœç¬¬äºŒä¸ªå‚æ•°æ˜¯ä¸€ä¸ªç©ºæ•°ç»„ []ï¼Œé‚£ä¹ˆè¿™ä¸ªeffectåªåœ¨ç»„ä»¶çš„ç¬¬ä¸€æ¬¡æ¸²æŸ“æ—¶è¿è¡Œã€‚
      // useEffectçš„ç¬¬äºŒä¸ªå‚æ•°æ˜¯ä¸€ä¸ªåŒ…æ‹¬äº†åœ¨å½“å‰ useEffect ä¸­æ‰€ä½¿ç”¨çš„çš„å˜é‡çš„æ•°ç»„ï¼Œå¦‚æžœå…¶ä¸­æŸä¸ªå˜é‡å˜æ›´ã€Object.isåˆ¤æ–­ã€‘äº†ï¼Œé‚£ä¹ˆåœ¨æœ€è¿‘ä¸€æ¬¡æ¸²æŸ“åŽï¼Œè¿™ä¸ªuseEffectä¼šè¢«é‡æ–°æ‰§è¡Œï¼›
      // å¦‚æžœä¼ å…¥çš„æ˜¯ä¸€ä¸ªç©ºæ•°ç»„ï¼Œé‚£ä¹ˆé™¤äº†ç¬¬ä¸€æ¬¡æŒ‚è½½ç»„ä»¶æ—¶ä¼šæ‰§è¡Œï¼Œå…¶ä»–æ—¶é—´æ›´æ–°ç»„ä»¶çš„æ—¶å€™ä¸ä¼šæ‰§è¡Œï¼Œå› ä¸ºä¸ä¾èµ–ä»»ä½•å˜é‡ï¼Œä¹Ÿå°±æ˜¯ä¾èµ–ä¸ä¼šå˜æ›´ï¼Œç»å¸¸è¢«ç”¨äºŽæ¨¡æ‹Ÿ componentDidMountï¼Œä½†æ˜¯è¿˜æ˜¯å­˜åœ¨åŒºåˆ«çš„ã€‚
  }, [])

  // å¤„ç†è¡¨å•æäº¤
  const onSubmitNote = (event) => {
    event.preventDefault()
    let o = {
      // id: notes.length + 1, // no need to post
      content: newNote,
      date: (new Date()).toString(),
      important: Math.random() < 0.5
    }
    noteService
      .create(o)
      .then(res => {
        setNotes([...notes, res.data])
        setNewNote('')
      })
  }

  // å¤„ç†æ–‡æœ¬æ¡†  å—æŽ§ç»„ä»¶
  const onInputChange = (event) => {
    setNewNote(event.target.value)
  }

  // å¤„ç†checkbox  å—æŽ§ç»„ä»¶
  const onCheckBoxChange = () => {
    setIsShowAll(!isShowAll)
    // console.info(isShowAll) // isshowall not change after setting new value    
  }

  const notesToShow = () => notes.filter(n => isShowAll || n.important)

  const showError = msg => {
    setErrorMessage(msg)
    setTimeout(() => {
      setErrorMessage(null)
    }, 5000)
  }

  // æ›´æ”¹noetçš„important
  const onChangeImportance = (id) => {
    const note = notes.find(n => n.id === id) 

    // ä¸å»ºè®®è¿™æ ·åšï¼Œå› ä¸ºå˜é‡ note æ˜¯å¯¹å¤„äºŽç»„ä»¶çŠ¶æ€ notes æ•°ç»„ä¸­æŸä¸ªé¡¹çš„å¼•ç”¨ï¼Œ
    // è€Œä¸”æˆ‘ä»¬è®°å¾—åœ¨ React ä¸­ç»ä¸èƒ½ç›´æŽ¥ä¿®æ”¹çŠ¶æ€ã€‚
    // note.important = !note.important
    const changedNote = {important: !note.important}
    noteService  // æˆ‘ä»¬å¯ä»¥ç”¨ HTTP PUT è¯·æ±‚æ›¿æ¢ æ•´ä¸ªä¾¿ç¬ºï¼Œæˆ–è€…åªç”¨ HTTP PATCH è¯·æ±‚æ›´æ”¹ä¾¿ç¬ºçš„ä¸€äº›å±žæ€§ã€‚
      .update(id, changedNote)
      .then(res => {
        console.info(res)
        setNotes(notes.map((n) => n.id === id ? res.data : n))
      })
      .catch(error => { // æ›´æ”¹å·²ç»åˆ é™¤çš„note
        showError(`error`)
        console.error('err', error)
        setNotes(notes.filter(n => n.id !== id))
      })
  }

  // do not use the index offered by map func
  return (
    <div>
      <Notification msg={errorMessage} />
      <input id="showAll" type="checkbox" checked={isShowAll} onChange={onCheckBoxChange}/>
      <label htmlFor="showAll">ShowALL</label>
      <form onSubmit={onSubmitNote}>
        <input type="text" value={newNote} onChange={onInputChange}/>
        <button type="submit" >Add</button>
      </form>
      
      <p>showing {isShowAll ? 'All' : 'important'}</p>
      {
        notesToShow()
          .map(n => <NoteItem key={n.id} 
                              note={n} 
                              onChangeImportance={()=>onChangeImportance(n.id)}/>)
      }
    </div>
    
  )
}
export default Notes;
